
.nav__header{
    width: 100%;
    position:fixed;
    bottom: 0;
    left: 0;
    z-index: $z-fixed;
    background-color: $body-color;
}
.nav__btns{
    display: flex;
    align-items: center;
}
.nav__nav{
    max-width: 968px;
    height: $header-height;
    display:flex;
    justify-content:space-between;
    align-items:center;
    padding: 0 .5rem;
}

.nav__logo,
.nav__toggle{
    color:$title-color;
    font-weight:$font-medium;
}

.nav__logo:hover{
    color:$first-color;
}

.nav__toggle{
    font-size: 1.1rem;
    cursor: pointer;
    &:hover{
        color:$first-color;
    }
}

@media screen and (max-width: $breakpoint-768){
    .nav__menu{
        position:fixed;
        bottom:-100%;
        left:0;
        width: 100%;
        background-color: $body-color;
        padding:2rem 1.5rem 4rem;
        box-shadow: 0 -1px 4px rgba(0,0,0,.15);
        border-radius: 1.5rem 1.5rem 0 0;
        transition:.3s;
    }
}

.nav__list{
    grid-template-columns: repeat(2,1fr);
    gap: 2rem;
}

.nav__link{
    display: flex;
    flex-direction: column;
    align-items: center;
    font-size: $small-font-size;
    color: $title-color;
    font-weight:$font-medium;
    &:hover{
        color: $first-color;
    }
    text-decoration: none;
}

.nav__icon{
    font-size: 1.2rem;
}

.nav__close{
    position: absolute;
    right: 1.3rem;
    bottom: .5rem;
    font-size: 1.5rem;
    cursor:pointer;
    color:$first-color;
    &:hover{
        color: $first-color-alt;
    }
}

.show__menu{
    bottom: 0;
}

.active__link{
    color: $first-color;
}

.change__theme{
    font-size:1.25rem;
    color: $title-color;
    margin-right: $mb-1;
    cursor: pointer;
    &:hover{
        color: $first-color;
    }
}
.nav__exit{
    font-size:1.25rem;
    color: $title-color;
    margin: 0 $mb-1;
    cursor: pointer;
    &:hover{
        color: $first-color;
    }
}