.button{
    display: inline-block;
    background-color: $first-color;
    color: #FFF;
    padding: .9rem;
    border-radius: .5rem;
    font-weight: $font-medium;
    font-size: 1rem;
    border: none;
    cursor: pointer;
    &:hover{
        background-color:$first-color-alt;
    }
}

.button__icon{
    font-size:1.25rem;
    margin-left: $mb-0-5;
    transition: .3s;
}
.button__white{
    background-color:#FFF;
    color: $first-color;

    &:hover{
        background-color: #FFE;
    }
}

.button__flex{
    display: inline-flex;
    align-items: center;
}

.button__small{
    padding:.75rem 1rem;
}

.button__link{
    padding: 0;
    background-color: transparent;
    color: $first-color;
    &:hover{
        background-color:transparent;
        color: $first-color-alt;
    }
}