/// COLORS
$header-height:3rem;
$hue-color:231;
$first-color: hsl($hue-color, 69%, 61%);
$first-color-second:hsl($hue-color,69%, 61%);
$first-color-alt:hsl($hue-color, 57%,53%);
$first-color-lighter:hsl($hue-color,92%, 85%);
$title-color: hsl($hue-color,8%, 15%);
$text-color: hsl($hue-color, 8%, 45%);
$text-color-light: hsl($hue-color, 8%, 65%);
$text-color-dark: hsl($hue-color, 8%, 0%);
$input-color:hsl($hue-color,70%, 90%);
$input-color-alt:hsl($hue-color,58%, 82%);
$body-color:hsl($hue-color, 60%, 99%);
$container-color: #FFF;
$scroll-bar-color: hsl($hue-color, 12%, 90%);
$scroll-thumb-color: hsl($hue-color, 12%, 80%);

$body-font: 'Poppins', 'sans-serif';
$big-font-size: 2rem;
$h1-font-size: 1.5rem;
$h2-font-size: 1.25rem;
$h3-font-size: 1.125rem;
$normal-font-size:.938rem;
$small-font-size:.813rem;
$smaller-font-size:.75rem;

$font-medium: 500;
$font-semi-bold: 600;


$mb-0-25: .25rem;
$mb-0-5: .5rem;
$mb-0-75: .75rem;
$mb-1: 1rem;
$mb-1-5: 1.5rem;
$mb-2: 2rem;
$mb-2-5: 2.5rem;
$mb-3: 3rem;


$z-tooltip: 10;
$z-fixed: 100;
$z-modal: 100;


//querys
$breakpoint-350: 350px;
$breakpoint-480: 480px;
$breakpoint-568: 568px;
$breakpoint-768: 768px;
$breakpoint-1024: 1024px;
$breakpoint-1200: 1200px;