.ld__loading{
    position: absolute;
    left: 48vw;
    bottom:45vh;
    display: flex;
    justify-content: center;

    &::after{
        content: "";
        width:2rem;
        height:2rem;
        border: 10px solid #dddddd;
        border-top-color: $first-color;
        border-radius: 50%;
        animation: loading 1s ease infinite;
    }
}

@keyframes loading{
    to{
        transform: rotate(1turn)
    }
}